const actionReducer = (state, action) => {
  const isRequest = action.type.toLowerCase().includes("requested");

  if (action.payload && !isRequest) {
    // normale Aktionen verndern den Zustand
    return { ...state, ...action.payload };
  } else {
    // Aktionen, die als API Abfragen mit dem Suffix "requested" gekennzeichnet sind, verndern den Zustand nicht
    return state;
  }
};

export { actionReducer };
